import { _decorator, Component, Node, Size, Sprite, SpriteFrame, UITransform } from 'cc';
import { FadePopup } from './FadePopup';
import { SizePopup } from './SizePopup';
const { ccclass, property } = _decorator;

@ccclass('CollectedItemShower')
export class CollectedItemShower extends Component {
    @property(Sprite)
    itemSprite: Sprite = null;

    @property(FadePopup)
    fadePopup: FadePopup = null;

    @property(SizePopup)
    sizePopup: SizePopup = null;

    show(spriteFrame: SpriteFrame){
        if(this.itemSprite && this.fadePopup && this.sizePopup){
            if (this.itemSprite) {
                this.itemSprite.spriteFrame = spriteFrame;
    
                this.itemSprite.sizeMode = Sprite.SizeMode.RAW;
    
                const originalSize = spriteFrame.getOriginalSize();
    
                let targetWidth = originalSize.width;
                let targetHeight = originalSize.height;
    
                if (targetHeight > 250) {
                    const scaleFactor = 250 / targetHeight;
                    targetHeight = 250;
                    targetWidth = originalSize.width * scaleFactor;
                }
    
                const spriteTransform = this.itemSprite.node.getComponent(UITransform);
                if (spriteTransform) {
                    spriteTransform.setContentSize(new Size(targetWidth, targetHeight));
                }
            }

            this.fadePopup.show();
            this.sizePopup.show();
        }
    }

    hide(){
        if(this.itemSprite && this.fadePopup && this.sizePopup){
            this.fadePopup.hide();
            this.sizePopup.hide();
        }
    }
}

