import { _decorator, Component, Label, Sprite, SpriteFrame, Color } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Toast')
export class Toast extends Component {

    @property({ type: Label })
    messageLabel: Label = null;

    @property({ type: Sprite })
    iconSprite: Sprite = null;

    @property({ type: SpriteFrame })
    errorIcon: SpriteFrame = null;

    @property({ type: SpriteFrame })
    warnIcon: SpriteFrame = null;

    @property({ type: SpriteFrame })
    logIcon: SpriteFrame = null;

    public init(message: string, type: 'error' | 'warn' | 'log') {
        this.messageLabel.string = message;

        switch (type) {
            case 'error':
                this.iconSprite.spriteFrame = this.errorIcon;
                break;
            case 'warn':
                this.iconSprite.spriteFrame = this.warnIcon;
                break;
            case 'log':
                this.iconSprite.spriteFrame = this.logIcon;
                break;
            default:
                break;
        }
    }
}
