import { _decorator, Component, Node, Vec3, tween, UITransform } from 'cc';
const { ccclass, property } = _decorator;

enum PopupState {
    None,
    Hidden,
    Showing,
    Shown,
    Hiding,
}

@ccclass('SizePopup')
export class SizePopup extends Component {
    @property
    showDuration: number = 0.5;

    @property
    hideDuration: number = 0.5;

    @property
    isOpenAtStart: boolean = false;

    private state: PopupState = PopupState.None;

    start() {
        this.initScale();
    }

    initScale() {
        if (this.isOpenAtStart) {
            this.node.setScale(new Vec3(1, 1, 1));
            this.state = PopupState.Shown;
        } else {
            this.node.setScale(new Vec3(0, 0, 0));
            this.state = PopupState.Hidden;
        }
    }

    show() {
        if (this.state !== PopupState.Hidden && this.state !== PopupState.Hiding && this.state !== PopupState.None) {
            return;
        }
        this.state = PopupState.Showing;

        this.node.setScale(new Vec3(0, 0, 0));

        tween(this.node)
            .to(this.showDuration, { scale: new Vec3(1, 1, 1) }, { easing: 'sineOut' })
            .call(() => {
                this.state = PopupState.Shown;
            })
            .start();
    }

    hide() {
        if (this.state !== PopupState.Shown && this.state !== PopupState.Showing) {
            return;
        }
        this.state = PopupState.Hiding;

        tween(this.node)
            .to(this.hideDuration, { scale: new Vec3(0, 0, 0) }, { easing: 'sineIn' })
            .call(() => {
                this.state = PopupState.Hidden;
            })
            .start();
    }
}