import { _decorator, Component, Node, EventTouch, Sprite, UITransform, Vec3, tween, EventHandler, Color, SpriteFrame } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Switch')
export class Switch extends Component {

    @property(Sprite)
    background: Sprite = null;

    @property(Sprite)
    handle: Sprite = null;

    @property(Boolean)
    isOn: boolean = false;

    @property(Boolean)
    useOffset: boolean = true;

    @property(Number)
    offset: number = 18;

    @property({ type: Number, tooltip: "Duration of the toggle animation in seconds" })
    duration: number = 0.2;

    @property({ type: [EventHandler], tooltip: "Events called when switch state changes" })
    switchEvents: EventHandler[] = [];

    @property({ type: Color, tooltip: "Background color when switch is ON" })
    onBgColor: Color = new Color(76, 217, 100);

    @property({ type: Color, tooltip: "Background color when switch is OFF" })
    offBgColor: Color = new Color(200, 200, 200);

    @property({ type: SpriteFrame, tooltip: "Handle sprite when switch is ON" })
    onHandleSprite: SpriteFrame = null;

    @property({ type: SpriteFrame, tooltip: "Handle sprite when switch is OFF" })
    offHandleSprite: SpriteFrame = null;

    private handleNode: Node = null;
    private backgroundNode: Node = null;

    onLoad() {
        this.handleNode = this.handle.node;
        this.backgroundNode = this.background.node;

        this.node.on(Node.EventType.TOUCH_END, this.onToggle, this);
        this.updateVisualState(false);
    }

    onToggle(event: EventTouch) {
        this.isOn = !this.isOn;
        this.updateVisualState(true);
        this.emitSwitchEvent();
    }

    updateVisualState(animated: boolean) {
        const targetX = this.isOn ? this.getOnPositionX() : this.getOffPositionX();
        if (animated) {
            tween(this.handleNode)
                .to(this.duration, { position: new Vec3(targetX, 0, 0) }, { easing: 'sineOut' })
                .start();
        } else {
            this.handleNode.setPosition(new Vec3(targetX, 0, 0));
        }

        this.background.color = this.isOn ? this.onBgColor : this.offBgColor;

        if (this.isOn && this.onHandleSprite) {
            this.handle.spriteFrame = this.onHandleSprite;
        } else if (!this.isOn && this.offHandleSprite) {
            this.handle.spriteFrame = this.offHandleSprite;
        }
    }

    getOnPositionX(): number {
        if(this.useOffset){
            return this.offset;
        }else{
            const backgroundWidth = this.backgroundNode.getComponent(UITransform).width;
            const handleWidth = this.handleNode.getComponent(UITransform).width;
            return (backgroundWidth - handleWidth) / 2;
        }
    }

    getOffPositionX(): number {
        if (this.useOffset) {
            return -(this.offset);
        } else {
            const backgroundWidth = this.backgroundNode.getComponent(UITransform).width;
            const handleWidth = this.handleNode.getComponent(UITransform).width;
            return -(backgroundWidth - handleWidth) / 2;
        }
    }

    emitSwitchEvent() {
        EventHandler.emitEvents(this.switchEvents, this.isOn);
    }
}
