import { ToastManager } from "./ToastManager";

export class ServerCommunicator {
    private static _instance: ServerCommunicator;
    private baseUrl: string = "https://horizonserv.fun/";
    
    private requestTracker: Map<string, number[]> = new Map();

    private readonly MAX_REQUESTS: number = 5;
    private readonly TIME_WINDOW: number = 5000;

    private constructor() {}

    public static get instance(): ServerCommunicator {
        if (!this._instance) {
            this._instance = new ServerCommunicator();
        }
        return this._instance;
    }

    private canSendRequest(endpoint: string): boolean {
        const currentTime = Date.now();
        if (!this.requestTracker.has(endpoint)) {
            this.requestTracker.set(endpoint, []);
        }

        const timestamps = this.requestTracker.get(endpoint)!;

        const updatedTimestamps = timestamps.filter(timestamp => currentTime - timestamp < this.TIME_WINDOW);
        this.requestTracker.set(endpoint, updatedTimestamps);

        if (updatedTimestamps.length >= this.MAX_REQUESTS) {
            ToastManager.getInstance().showToast("Too many requests!", "warn");
            return false;
        }

        updatedTimestamps.push(currentTime);
        this.requestTracker.set(endpoint, updatedTimestamps);
        return true;
    }

    public async sendGetRequest(endpoint: string, params: Record<string, string> = {}): Promise<any> {
        if (!this.canSendRequest(endpoint)) {
            return Promise.reject(new Error("Too many requests"));
        }

        const queryString = new URLSearchParams(params).toString();
        const url = `${this.baseUrl}${endpoint}${queryString ? `?${queryString}` : ''}`;

        try {
            const response = await fetch(url, {
                method: "GET",
                headers: {
                    "Content-Type": "application/json"
                }
            });

            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }

            return await response.json();
        } catch (error) {
            console.error(`GET request to ${url} failed:`, error);
            throw error;
        }
    }

    public async sendPostRequest(endpoint: string, data: any): Promise<any> {
        if (!this.canSendRequest(endpoint)) {
            return Promise.reject(new Error("Too many requests"));
        }

        const url = `${this.baseUrl}${endpoint}`;

        try {
            const response = await fetch(url, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify(data)
            });

            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }

            return await response.json();
        } catch (error) {
            console.error(`POST request to ${url} failed:`, error);
            throw error;
        }
    }
}
