import { _decorator, Component, Node } from 'cc';
import { TelegramWebApp } from './telegram-web';
const { ccclass, property } = _decorator;

@ccclass('ShareInviteLink')
export class ShareInviteLink extends Component {

    @property(String)
    public baseLinkToShare: String = 'https://t.me/horizenworld_bot/game?startapp=';

    start() {
        this.node.on(Node.EventType.TOUCH_END, this.onClick, this);
    }

    async onClick() {
        const tgWebApp = TelegramWebApp.Instance;
        await tgWebApp.init();

        const user = tgWebApp.getTelegramUser();
        const urlForShare = this.baseLinkToShare + user.id.toString();

        tgWebApp.share(urlForShare, "Тебя ждет незабываемое путешествие на райские острова, Но для начала нужно собрать чемоданы.");
    }
}

