import { _decorator, Component, Sprite, tween, UIOpacity } from 'cc';
const { ccclass, property } = _decorator;

enum PopupState {
    None,
    Hidden,
    Showing,
    Shown,
    Hiding,
}

@ccclass('FadePopup')
export class FadePopup extends Component {
    @property(Sprite)
    targetSprite: Sprite | null = null;

    @property
    showDuration: number = 0.5;

    @property
    hideDuration: number = 0.5;

    @property
    isOpenAtStart: boolean = false;

    private state: PopupState = PopupState.None;

    start() {
        this.initOpacity();
    }

    initOpacity() {
        if (this.targetSprite) {
            const uiOpacity = this.getOrAddUIOpacity();

            if (this.isOpenAtStart) {
                uiOpacity.opacity = 255; // Полностью видим
                this.state = PopupState.Shown;
            } else {
                uiOpacity.opacity = 0; // Полностью прозрачный
                this.state = PopupState.Hidden;
            }
        }
    }

    show() {
        if (this.state !== PopupState.Hidden && this.state !== PopupState.Hiding && this.state !== PopupState.None) {
            return;
        }
        this.state = PopupState.Showing;

        if (this.targetSprite) {
            const uiOpacity = this.getOrAddUIOpacity();

            tween(uiOpacity)
                .to(this.showDuration, { opacity: 255 }, { easing: 'sineOut' })
                .call(() => {
                    this.state = PopupState.Shown;
                })
                .start();
        }
    }

    hide() {
        if (this.state !== PopupState.Shown && this.state !== PopupState.Showing) {
            return;
        }
        this.state = PopupState.Hiding;

        if (this.targetSprite) {
            const uiOpacity = this.getOrAddUIOpacity();

            tween(uiOpacity)
                .to(this.hideDuration, { opacity: 0 }, { easing: 'sineIn' })
                .call(() => {
                    this.state = PopupState.Hidden;
                })
                .start();
        }
    }

    getOrAddUIOpacity() {
        let uiOpacity = this.targetSprite!.node.getComponent(UIOpacity);
        if (!uiOpacity) {
            uiOpacity = this.targetSprite!.node.addComponent(UIOpacity);
        }
        return uiOpacity;
    }
}
