import { _decorator, Component, Node, EventHandler } from 'cc';
import { Switch } from './Switch';
import VibrationManager from './VibrationManager';
const { ccclass, property } = _decorator;

@ccclass('VibrationSwitchController')
export class VibrationSwitchController extends Component {

    @property(Switch)
    switchComponent: Switch = null;

    async onLoad() {
        if (!VibrationManager['initialized']) {
            await VibrationManager.init();
        }

        this.switchComponent.isOn = VibrationManager.isVibrationEnabled();
        this.switchComponent.updateVisualState(false);

        const eventHandler = new EventHandler();
        eventHandler.target = this.node;
        eventHandler.component = 'VibrationSwitchController';
        eventHandler.handler = 'onSwitchToggled';
        eventHandler.customEventData = '';

        this.switchComponent.switchEvents.push(eventHandler);
    }

    async onSwitchToggled(isOn: boolean) {
        if (isOn !== VibrationManager.isVibrationEnabled()) {
            await VibrationManager.toggleVibration();
        }
    }
}