import { _decorator, Component, Node, Prefab, instantiate, director, Vec3, UITransform, Tween, tween } from 'cc';
import { Toast } from './Toast';
const { ccclass, property } = _decorator;

@ccclass('ToastManager')
export class ToastManager extends Component {

    private static _instance: ToastManager;

    @property({ type: Prefab })
    toastPrefab: Prefab = null;

    @property({ type: Number, tooltip: "Отступ сверху в пикселях" })
    topOffset: number = 50;

    @property({ type: Number, tooltip: "Длительность отображения сообщения в секундах" })
    displayDuration: number = 2;

    private _toasts: Node[] = [];

    onLoad() {
        if (ToastManager._instance) {
            this.destroy();
            return;
        }
        ToastManager._instance = this;
    }

    public static getInstance(): ToastManager {
        if (!ToastManager._instance) {
            const canvas = director.getScene().getChildByName('Canvas');
            const toastManagerNode = new Node('ToastManager');
            canvas.addChild(toastManagerNode);
            toastManagerNode.addComponent(ToastManager);
        }
        return ToastManager._instance;
    }

    public showToast(message: string, type: 'error' | 'warn' | 'log', duration?: number) {
        if (!this.toastPrefab) {
            console.error('Toast Prefab not set in ToastManager');
            return;
        }

        const toastNode = instantiate(this.toastPrefab);
        const toastComponent = toastNode.getComponent(Toast);
        if (toastComponent) {
            toastComponent.init(message, type);
        }

        const canvas = director.getScene().getChildByName('Canvas');
        canvas.addChild(toastNode);

        const uiTransform = canvas.getComponent(UITransform);
        const startPosition = new Vec3(0, uiTransform.height / 2 + 100, 0);
        toastNode.setPosition(startPosition);

        const targetY = uiTransform.height / 2 - this.topOffset;
        tween(toastNode)
            .to(0.3, { position: new Vec3(0, targetY, 0) }, { easing: 'bounceOut' })
            .start();

        const displayTime = duration || this.displayDuration;
        tween(toastNode)
            .delay(displayTime)
            .to(0.3, { position: new Vec3(0, uiTransform.height / 2 + 100, 0) }, { easing: 'sineIn' })
            .call(() => {
                toastNode.destroy();
                this._toasts.splice(this._toasts.indexOf(toastNode), 1);
            })
            .start();

        this._toasts.push(toastNode);
    }
}
