import { _decorator, Component, director, Label, ProgressBar } from "cc";
import { TelegramWebApp } from "./telegram-web";
import { ServerCommunicator } from "./ServerCommunicator";
import { ToastManager } from "./ToastManager";

const { ccclass, property } = _decorator;

@ccclass
export default class LoadingScreen extends Component {
    @property(ProgressBar)
    progressBar: ProgressBar = null;

    @property(Label)
    progressLabel: Label = null;

    @property(Label)
    onMobileLabel: Label = null;

    isMobileDevice(): boolean {
        return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
    }

    onLoad() {
        this.init();
    }

    async init() {
        if (!this.isMobileDevice()) {
            this.onMobileLabel.string = "Sorry, it only works on mobile devices!";
            return;
        }

        const tgInited = await TelegramWebApp.Instance.init();
        if (tgInited) {
            TelegramWebApp.Instance.expand();

            const webinitdata = TelegramWebApp.Instance.getTelegramInitData();
            const endpoint = 'auth';

            ServerCommunicator.instance.sendPostRequest(endpoint, { webinitdata })
                .then(response => {
                    if (response.auth === true) {
                        this.loadMainScene();
                    } else {
                        ToastManager.getInstance().showToast("Invalid authorization, try again later", "error");
                    }
                })
                .catch(error => {
                    ToastManager.getInstance().showToast("An error occurred during authentication", "error");
                    console.error('POST Error:', error);
                });
        } else {
            ToastManager.getInstance().showToast("Failed to initialize Telegram Web App", "error");
        }
    }

    loadMainScene() {
        director.preloadScene("MainScene", this.onProgress.bind(this), this.onComplete.bind(this));
    }

    onProgress(completedCount: number, totalCount: number, item: any) {
        const progress = completedCount / totalCount;
        this.progressBar.progress = progress;
        this.progressLabel.string = Math.floor(progress * 100) + "%";
    }

    onComplete() {
        director.loadScene("MainScene");
    }
}
