import { _decorator, Component, Node } from 'cc';
import { TelegramWebApp } from './telegram-web';
const { ccclass, property } = _decorator;

@ccclass('OpenLinkWithTelegram')
export class OpenLinkWithTelegram extends Component {
    @property
    url: string = "";

    public openLink(){
        const tgWebApp = TelegramWebApp.Instance;
        if(tgWebApp != null){
            tgWebApp.openTelegramLink(this.url);
        }
    }
}

