import { _decorator } from 'cc';
import { TelegramWebApp } from './telegram-web';
import { UserDataManager, UserSettingsData } from './UserDataManager';
const { ccclass } = _decorator;

@ccclass('VibrationManager')
export default class VibrationManager {
    private static vibrationEnabled: boolean = true;
    private static currentUserData: UserSettingsData = { vibration: false };
    private static initialized: boolean = false;

    public static async init(): Promise<void> {
        try {
            const data = await this.getUserSettingsData();
            if (data) {
                this.currentUserData = data;
                this.vibrationEnabled = data.vibration;
            } else {
                this.currentUserData = { vibration: this.vibrationEnabled };
                await this.saveUserSettingsData(this.currentUserData);
            }
        } catch (error) {
            console.error("Ошибка инициализации VibrationManager:", error);
        } finally {
            this.initialized = true;
        }
    }

    private static getUserSettingsData(): Promise<UserSettingsData> {
        return new Promise((resolve, reject) => {
            UserDataManager.instance.getUserSettingsData((error, data) => {
                if (error) {
                    console.error("Ошибка при получении данных настроек пользователя:", error);
                    reject(error);
                } else {
                    console.log("Получены данные настроек пользователя:", data);
                    resolve(data);
                }
            });
        });
    }

    private static saveUserSettingsData(data: UserSettingsData): Promise<void> {
        return new Promise((resolve, reject) => {
            UserDataManager.instance.saveUserSettingsData(data, (error) => {
                if (error) {
                    console.error("Ошибка при сохранении данных настроек пользователя:", error);
                    reject(error);
                } else {
                    console.log("Данные настроек пользователя сохранены");
                    resolve();
                }
            });
        });
    }

    private static updateUserSettingsData(data: UserSettingsData): Promise<void> {
        return new Promise((resolve, reject) => {
            UserDataManager.instance.updateUserSettingsData(data, (error) => {
                if (error) {
                    console.error("Ошибка при обновлении данных настроек пользователя:", error);
                    reject(error);
                } else {
                    console.log("Данные настроек пользователя обновлены");
                    resolve();
                }
            });
        });
    }

    private static isVibrationSupported(): boolean {
        return TelegramWebApp.Instance != null;
    }

    public static async toggleVibration(): Promise<void> {
        if (!this.initialized) {
            console.error("VibrationManager не инициализирован. Вызовите VibrationManager.init() перед использованием.");
            return;
        }
        try {
            this.vibrationEnabled = !this.vibrationEnabled;
            this.currentUserData.vibration = this.vibrationEnabled;
            await this.updateUserSettingsData(this.currentUserData);
        } catch (error) {
            console.error("Ошибка при переключении состояния вибрации:", error);
        }
    }

    public static isVibrationEnabled(): boolean {
        if (!this.initialized) {
            console.error("VibrationManager не инициализирован. Вызовите VibrationManager.init() перед использованием.");
            return false;
        }
        return this.vibrationEnabled;
    }

    private static performHapticFeedback(type: string): void {
        if (this.isVibrationSupported() && this.isVibrationEnabled()) {
            TelegramWebApp.Instance.doHapticImpact(type);
        }
    }

    private static performHapticNotification(type: string): void {
        if (this.isVibrationSupported() && this.isVibrationEnabled()) {
            TelegramWebApp.Instance.doHapticNotification(type);
        }
    }

    public static light(): void {
        this.performHapticFeedback("light");
    }

    public static medium(): void {
        this.performHapticFeedback("medium");
    }

    public static heavy(): void {
        this.performHapticFeedback("heavy");
    }

    public static rigid(): void {
        this.performHapticFeedback("rigid");
    }

    public static soft(): void {
        this.performHapticFeedback("soft");
    }

    public static error(): void {
        this.performHapticNotification("error");
    }

    public static success(): void {
        this.performHapticNotification("success");
    }

    public static warning(): void {
        this.performHapticNotification("warning");
    }
}
